import React from 'react';
import './App.css'

function Price(props) {
    return (
        <div className="col-lg-4">
            <div className="card mb-5 mb-lg-0">
                <div className="card-body">
                    <h5 className="card-title text-muted text-uppercase text-center">{props.productdata.freeornot}</h5>
                    <h6 className="card-price text-center">{props.productdata.price}<span className="period">{props.productdata.monthly}</span></h6>
                    <hr />
                    <ul className="fa-ul">{
                        props.productdata.features.map((plan) => {
                            return <li className={plan.isEnabled ? '' : 'text-muted'}>
                                <span className="fa-li"><i className={`fa ${plan.isEnabled ?'fa-check' : 'fa-times'}`}></i></span>
                                {
                                    plan.isBold ? <strong> {
                                        plan.name
                                    }
                                    </strong> : <span>{plan.name}</span>
                                } </li>
                        })
                    }</ul>

                    <div className="d-grid">
                        <button className="btn btn-primary text-uppercase"
                            onClick={() => {
                                props.handleSubscribe(props.productdata.freeornot)
                            }}> Button </button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Price;